import * as yup from "yup";

export const postSchema = yup.object().shape({
  name: yup
    .string()
    .required("Vui lng nhp tiu ")
    .min(16, "tiu  t nht 16 k t"),
});


export const checkOutSchema = yup.object().shape({
  name: yup
    .string()
    .max(255, 'Ti a 255 k t')
    .trim()
    .required("Khng c  trng"),
  phone: yup
    .string()
    .required("Khng c  trng")
    .max(10,'Ti a 10 k t')
    .matches(/(84|0[3|5|7|8|9])+([0-9]{8})\b/g, 'khng ng nh dng'),
  address: yup
    .string()
    .max(255, 'Ti a 255 k t')
    .trim()
    .required("Khng c  trng"),
  note: yup
    .string()
    .max(255, 'Ti a 255 k t')
    .trim(),
  payment_id: yup
    .number()
    .min(1, "Bn cha chn"),
});

export const infoSchema = yup.object().shape({
  name: yup
    .string()
    .max(255, 'Ti a 255 k t')
    .trim()
    .required("Khng c  trng"),
  phone: yup
    .string()
    .required("Khng c  trng")
    .max(10,'Ti a 10 k t')
    .matches(/(84|0[3|5|7|8|9])+([0-9]{8})\b/g, 'khng ng nh dng'),
  address: yup
    .string()
    .max(255, 'Ti a 255 k t')
    .trim()
    .required("Khng c  trng"),
    email: yup
    .string()
    .required("Vui lng nhp email")
    .min(16, "email t nht 16 k t")
    .email('Khng ng nh dang email'),
});