import * as yup from "yup";

export const loginSchema = yup.object().shape({
  email: yup
    .string()
    .required("Vui lng nhp email")
    .min(16, "email t nht 18 k t")
    .email('Khng ng nh dang email'),
  password: yup
    .string()
    .required("Vui lng nhp mt khu")
    .min(8, "mt khu ti a 8 k t")
});

export const registerSchema = yup.object().shape({
  name: yup
    .string()
    .min(8, "Nhp t nht 8 k t")
    .max(50, "Nhp ti a 50 k t")
    .required("Vui lng nhp h tn"),
  email: yup
    .string()
    .required("Vui lng nhp email")
    .min(16, "email t nht 16 k t")
    .email('Khng ng nh dang email'),
  password: yup
    .string()
    .required("Vui lng nhp mt khu")
    .min(8, "mt khu ti a 8 k t"),
  confirm_password: yup
    .string()
    .required("Vui lng nhp mt khu")
    .min(8, "mt khu ti a 8 k t")
    .when("password", {
      is: (val) => (val && val.length > 0 ? true : false),
      then: yup.string().oneOf(
        [yup.ref("password")],
        "Xc mt khu khng khp"
      ),
    })
});

export const changePasswordSchema = yup.object().shape({
  old_password: yup
    .string()
    .required("Vui lng nhp mt khu")
    .min(8, "mt khu ti a 8 k t"),
  password: yup
    .string()
    .required("Vui lng nhp mt khu")
    .min(8, "mt khu ti a 8 k t"),
  password_confirmation: yup
    .string()
    .required("Vui lng nhp mt khu")
    .min(8, "mt khu ti a 8 k t")
    .when("password", {
      is: (val) => (val && val.length > 0 ? true : false),
      then: yup.string().oneOf(
        [yup.ref("password")],
        "Xc mt khu khng khp"
      ),
    })
});


export const forgotPasswordSchema = yup.object().shape({
  email: yup
    .string()
    .required("Vui lng nhp email")
    .min(16, "email t nht 16 k t")
    .email('Khng ng nh dang email'),
});

export const resetPasswordSchema = yup.object().shape({
  password: yup
    .string()
    .required("Vui lng nhp mt khu")
    .min(8, "mt khu ti a 8 k t"),
  password_confirmation: yup
    .string()
    .required("Vui lng nhp mt khu")
    .min(8, "mt khu ti a 8 k t")
    .when("password", {
      is: (val) => (val && val.length > 0 ? true : false),
      then: yup.string().oneOf(
        [yup.ref("password")],
        "Xc mt khu khng khp"
      ),
    }),
    otp: yup
    .string()
    .required("Vui lng m otp")
});